body {
    line-height: 1.7;
    font-family: "Open Sans", sans-serif;
    -webkit-font-smoothing: antialiased;
    color: #696c6d;
    font-size: 14px;
  }
  
  p, .paragraph {
    font-weight: 400;
    color: #696c6d;
    font-size: 14px;
    line-height: 1.7;
    font-family: "Open Sans", sans-serif;
  }
  
  h1, h2, h3, h4, h5, h6 {
    color: #1c1d1f;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    line-height: 1.2;
  }
  
  h1, .h1 {
    font-size: 45px;
  }
  
  h2, .h2 {
    font-size: 36px;
  }
  
  h3, .h3 {
    font-size: 28px;
  }
  
  h4, .h4 {
    font-size: 22px;
  }
  
  h5, .h5 {
    font-size: 18px;
  }
  
  h6, .h6 {
    font-size: 14px;
  }
  
  /* Button style */
  .btn {
    font-size: 14px;
    font-family: "Open Sans", sans-serif;
    text-transform: capitalize;
    padding: 8px 25px;
    border-radius: 50px;
    border: 1px solid;
    position: relative;
    z-index: 1;
    transition: 0.2s ease;
  }
  .btn:hover, .btn:active, .btn:focus {
    outline: 0;
    box-shadow: none !important;
  }
  
  .btn-primary {
    background: #ce8460;
    color: #fff;
    border-color: #ce8460;
  }
  .btn-primary:active, .btn-primary:hover, .btn-primary.focus, .btn-primary.active {
    background: #ce8460 !important;
    border-color: #ce8460 !important;
  }
  
  .btn-outline-primary {
    background: transparent;
    color: #696c6d;
    border-color: #ce8460;
  }
  .btn-outline-primary:active, .btn-outline-primary:hover, .btn-outline-primary.focus, .btn-outline-primary.active {
    background: #ce8460 !important;
    border-color: #ce8460 !important;
    color: #fff;
  }
  
  .btn-outline-light {
    background: transparent;
    color: #696c6d;
    border-color: #ddd;
  }
  .btn-outline-light:active, .btn-outline-light:hover, .btn-outline-light.focus, .btn-outline-light.active {
    background: #f0f0f0 !important;
    border-color: #ddd !important;
  }
  
  body {
    background-color: #fff;
    overflow-x: hidden;
  }
  
  ::-moz-selection {
    background: #daa287;
    color: #fff;
  }
  
  ::selection {
    background: #daa287;
    color: #fff;
  }
  
  /* preloader */
  .preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  img {
    vertical-align: middle;
    border: 0;
  }
  
  a,
  a:hover,
  a:focus {
    text-decoration: none;
  }
  
  a,
  button,
  select {
    cursor: pointer;
    transition: 0.2s ease;
  }
  a:focus,
  button:focus,
  select:focus {
    outline: 0;
  }
  
  a:hover {
    color: #ce8460;
  }
  
  .slick-slide {
    outline: 0;
  }
  
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .section-sm {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  
  .bg-cover {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }
  
  .border-primary {
    border-color: #ddd !important;
  }
  
  /* overlay */
  .overlay {
    position: relative;
  }
  .overlay::before {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0.5;
  }
  
  .outline-0 {
    outline: 0 !important;
  }
  
  .d-unset {
    display: unset !important;
  }
  
  .bg-primary {
    background: #ce8460 !important;
  }
  
  .text-primary {
    color: #ce8460 !important;
  }
  
  .text-color {
    color: #696c6d;
  }
  
  .mb-10 {
    margin-bottom: 10px !important;
  }
  
  .mb-20 {
    margin-bottom: 20px !important;
  }
  
  .mb-30 {
    margin-bottom: 30px !important;
  }
  
  .mb-40 {
    margin-bottom: 40px !important;
  }
  
  .mb-50 {
    margin-bottom: 50px !important;
  }
  
  .mb-60 {
    margin-bottom: 60px !important;
  }
  
  .mb-70 {
    margin-bottom: 70px !important;
  }
  
  .mb-80 {
    margin-bottom: 80px !important;
  }
  
  .mb-90 {
    margin-bottom: 90px !important;
  }
  
  .mb-100 {
    margin-bottom: 100px !important;
  }
  
  .zindex-1 {
    z-index: 1;
  }
  
  .overflow-hidden {
    overflow: hidden;
  }
  
  .rounded-lg, .post-slider {
    border-radius: 10px !important;
  }
  
  .border-default {
    border-color: #ddd !important;
  }
  
  .form-control:focus {
    outline: 0;
    box-shadow: none;
  }
  .form-control::-moz-placeholder {
    font-size: 80%;
  }
  .form-control::placeholder {
    font-size: 80%;
  }
  
  .nav-item {
    margin: 0;
  }
  .nav-item .nav-link {
    text-transform: capitalize;
  }
  
  .navbar-white .navbar-nav .nav-link {
    color: #1c1d1f;
  }
  .navbar-white .navbar-nav .nav-link:hover {
    color: #ce8460;
  }
  
  .navbar-expand-lg .navbar-nav .nav-link {
    padding: 15px 20px;
  }
  
  .navbar .dropdown:hover .dropdown-menu {
    visibility: visible;
    opacity: 1;
    transform: scaleX(1);
  }
  .navbar .dropdown-menu {
    box-shadow: 0px 3px 9px 0px rgba(0, 0, 0, 0.12);
    padding: 10px 0;
    border: 0;
    top: 50px;
    left: 0px;
    border-radius: 0;
    display: block;
    visibility: hidden;
    transition: 0.3s ease;
    opacity: 0;
    transform: scale(0.8);
    background: #fff;
    border-radius: 10px;
  }
  @media (max-width: 991px) {
    .navbar .dropdown-menu {
      display: none;
      opacity: 1;
      visibility: visible;
      transform: scale(1);
      transform-origin: unset;
    }
  }
  .navbar .dropdown-menu.show {
    visibility: hidden;
  }
  @media (max-width: 991px) {
    .navbar .dropdown-menu.show {
      visibility: visible;
      display: block;
    }
  }
  .navbar .dropdown-item {
    position: relative;
    color: #1c1d1f;
    transition: 0.2s ease;
    font-size: 14px;
  }
  @media (max-width: 991px) {
    .navbar .dropdown-item {
      text-align: center;
    }
  }
  .navbar .dropdown-item:not(:last-child) {
    margin-bottom: 10px;
  }
  .navbar .dropdown-item:hover {
    color: #ce8460 !important;
    background: transparent;
  }
  
  /* search */
  .search-btn {
    border: 0;
    background: transparent;
  }
  
  .search-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    z-index: 2;
    visibility: hidden;
    transition: 0.2s ease;
    opacity: 0;
  }
  .search-wrapper.open {
    visibility: visible;
    opacity: 1;
  }
  
  .search-box {
    height: 100%;
    width: 100%;
    border: 0;
    background: #fff;
    font-size: 20px;
    padding: 0;
  }
  .search-box:focus {
    box-shadow: none !important;
    outline: 0;
  }
  
  .search-close {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    border: 0;
    background: transparent;
    page-break-after: 10px;
    font-size: 20px;
  }
  
  .post-title {
    color: #1c1d1f;
  }
  .post-title:hover {
    color: #ce8460;
  }
  
  .post-meta li {
    margin-right: 10px;
    font-size: 90%;
  }
  .post-meta a {
    color: #696c6d;
    border-bottom: 1px dotted #ddd;
  }
  .post-meta a:hover {
    color: #ce8460;
  }
  
  .recent-thumb-alt {
    font-size: 0;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: #ce8460;
    text-align: center;
  }
  .recent-thumb-alt::first-letter {
    font-size: 40px;
    line-height: 60px;
    color: #fff;
  }
  
  .social-icons {
    margin-bottom: 0;
  }
  .social-icons a {
    display: block;
    color: #888c8e;
    padding: 5px;
    font-size: 14px;
  }
  .social-icons a:hover {
    color: #ce8460;
  }
  
  .social-icons-rounded {
    margin-bottom: 0;
  }
  .social-icons-rounded a {
    display: block;
    color: #888c8e;
    font-size: 14px;
    height: 40px;
    width: 40px;
    line-height: 40px;
    border-radius: 50%;
    border: 1px solid #ddd;
  }
  .social-icons-rounded a:hover {
    color: #ce8460;
    border-color: #ce8460;
  }
  
  .post-slider {
    overflow: hidden;
    margin-bottom: 20px;
  }
  .post-slider img {
    border-radius: inherit;
  }
  .post-slider:hover .prevArrow {
    left: 10px;
  }
  .post-slider:hover .nextArrow {
    right: 10px;
  }
  
  .slider-sm .slick-arrow {
    height: 35px;
    width: 35px;
    line-height: 35px;
  }
  
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 9;
    height: 50px;
    width: 50px;
    line-height: 50px;
    border-radius: 50%;
    background: rgba(206, 132, 96, 0.5);
    color: #fff;
    border: 0;
  }
  @media (max-width: 575px) {
    .slick-arrow {
      display: none;
    }
  }
  .slick-arrow:focus {
    outline: 0;
  }
  .slick-arrow:hover {
    background: #ce8460;
  }
  
  .prevArrow {
    left: -60px;
  }
  
  .nextArrow {
    right: -60px;
  }
  
  .pagination {
    margin-top: 50px;
  }
  .pagination .page-item .page-link {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    background: #f0f0f0;
    text-align: center;
    padding: 0;
    margin-right: 10px;
    border: 0;
    color: #696c6d;
  }
  .pagination .page-item .page-link:hover {
    background: #ce8460;
    color: #fff;
  }
  .pagination .page-item .page-link:focus {
    box-shadow: none;
  }
  .pagination .page-item:first-child .page-link, .pagination .page-item:last-child .page-link {
    border-radius: 50%;
  }
  .pagination .page-item.active .page-link {
    background: #ce8460;
    color: #fff;
  }
  
  .content * {
    word-break: break-word;
    overflow-wrap: break-word;
  }
  .content img {
    max-width: 100%;
  }
  .content h1,
  .content h2,
  .content h3,
  .content h4,
  .content h5,
  .content h6 {
    font-weight: 500;
  }
  .content strong {
    font-weight: 600;
  }
  .content a {
    color: #696c6d;
    border-bottom: 1px dotted #ddd;
  }
  .content a:hover {
    color: #ce8460;
  }
  .content ol,
  .content ul {
    padding-left: 20px;
  }
  .content ol li,
  .content ul li {
    margin-bottom: 10px;
  }
  .content ul {
    padding-left: 0;
    margin-bottom: 20px;
    list-style-type: none;
  }
  .content ul li {
    padding-left: 20px;
    position: relative;
  }
  .content ul li::before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background: #ce8460;
    left: 3px;
    top: 8px;
  }
  .content table {
    width: 100%;
    max-width: 100%;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
  }
  .content table td,
  .content table th {
    padding: 0.75rem;
    vertical-align: top;
    margin-bottom: 0;
  }
  .content tr:not(:last-child) {
    border-bottom: 1px solid #ddd;
  }
  .content th {
    font-weight: 500;
  }
  .content thead {
    background: #f0f0f0;
    margin-bottom: 0;
  }
  .content thead tr {
    border-bottom: 1px solid #ddd;
  }
  .content tbody {
    background: #fdfdfd;
    margin-bottom: 0;
  }
  .content .notices {
    margin: 2rem 0;
    position: relative;
    overflow: hidden;
  }
  .content .notices p {
    padding: 10px;
    margin-bottom: 0;
  }
  .content .notices p::before {
    position: absolute;
    top: 2px;
    color: #fff;
    font-family: "themify";
    font-weight: 900;
    content: "\e717";
    left: 10px;
  }
  .content .notices.note p {
    border-top: 30px solid #6ab0de;
    background: #f0f0f0;
  }
  .content .notices.note p::after {
    content: "Note";
    position: absolute;
    top: 2px;
    color: #fff;
    left: 2rem;
  }
  .content .notices.tip p {
    border-top: 30px solid #78C578;
    background: #f0f0f0;
  }
  .content .notices.tip p::after {
    content: "Tip";
    position: absolute;
    top: 2px;
    color: #fff;
    left: 2rem;
  }
  .content .notices.info p {
    border-top: 30px solid #F0B37E;
    background: #f0f0f0;
  }
  .content .notices.info p::after {
    content: "Info";
    position: absolute;
    top: 2px;
    color: #fff;
    left: 2rem;
  }
  .content .notices.warning p {
    border-top: 30px solid #E06F6C;
    background: #f0f0f0;
  }
  .content .notices.warning p::after {
    content: "Warning";
    position: absolute;
    top: 2px;
    color: #fff;
    left: 2rem;
  }
  .content blockquote {
    font-size: 20px !important;
    color: #696c6d;
    padding: 20px 40px;
    border-left: 2px solid #ce8460;
    margin: 40px 0;
    font-weight: bold;
    background: #f0f0f0;
  }
  .content blockquote p {
    margin-bottom: 0 !important;
  }
  .content pre {
    display: block;
    padding: 9.5px;
    margin: 10px 0px 10px;
    white-space: pre-wrap;
  }
  .content code {
    margin-bottom: 0 !important;
    font-size: 100%;
  }
  
  .code-tabs {
    border: 1px solid #ddd;
    overflow: hidden;
    margin-bottom: 20px;
  }
  .code-tabs .nav-tabs {
    margin-bottom: 0;
  }
  .code-tabs .nav-tabs .nav-item {
    padding-left: 0;
    border-right: 1px solid #ddd;
  }
  .code-tabs .nav-tabs .nav-item .nav-link {
    text-decoration: none;
