  .widget-post {
    padding: 15px 0 !important;
  }
  .widget-post img {
    /* height: 60px; */
    width: 50%;
    /* border-radius: 50%; */
    -o-object-fit: cover;
       object-fit: cover;
  }
  
  .footer-list a {
    display: block;
    color: #888c8e;
    padding: 5px 0;
  }
  .footer-list a:hover {
    color: #ce8460;
  }
  
  .subscription .form-control {
    background-color: #f0f0f0;
    border: 0;
    margin-bottom: 10px;
    padding-left: 40px;
  }
  .subscription .email-icon {
    position: absolute;
    left: 10px;
    top: 10px;
    font-size: 18px;
    color: #ce8460;
  }
  
  .quote {
    background-color: #f0f0f0;
