  }
  
  .scroll-top {
    border-top: 1px solid #ddd;
    position: relative;
    margin: 20px 0 40px;
  }
  .scroll-top a {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: #ce8460;
    color: #fff;
    text-align: center;
    line-height: 50px;
    display: block;
  }
    




  .panoramic-image {
    width: 100%!important;
    height: auto!important;
    object-fit: cover!important;
    aspect-ratio: 16 / 9!important; /* Wide aspect ratio */
    max-height: 500px!important; /* Optional: limit the height */
    overflow: hidden!important;
  }